.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}
.actualImage {
  width: 50%;
  height: 50%;
  align-self: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.loader {
  align-self: center;
  border: 3px solid #f3f3f3;
  border-radius: 50%;
  border-top: 3px solid #3498db;
  width: 20px;
  height: 20px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}
.finalTextArea{
  width: 30%;
  align-self: center;
}
.fileInput {
  align-self: center;
  padding-top: 2%;
}
.submitText {
    width: 10%;
    align-self: center;
    margin: 2%;
    background-color: #007cb0;
    font-weight: 600;
    padding: 0.5%;
    border-radius: 0%;
    border: none;
    color: white;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
